<template>
  <div class="ListenBook">
    <div
      class="bg"
      :style="{ backgroundImage: 'url(' + this.ListenData.cover + ')' }"
    ></div>
    <van-nav-bar
      left-arrow
      :title="ListenData.title"
      @click-left="onClickLeft"
    />
    <div class="listen-img">
      <img class="auto-img" :src="ListenData.cover" alt="" />
    </div>
    <div class="listen-nt">{{ListenData.title}}</div>
    <div class="listen-box">
      <div class="listen-hc">
        <div class="listen-h">作者:{{ ListenData.author }}</div>
        <div class="listen-c">集数:{{ ListenData.articleCount }}集</div>
      </div>
      <div class="listen-ap">
        <div class="listen-anchor">主播:{{ ListenData.anchor }}</div>
        <div class="listen-price">每集价格:{{ ListenData.price }}阅点</div>
      </div>
    </div>
    <div class="listen-content">
      书籍简介：
      <div class="content-box">{{ ListenData.description }}</div>
    </div>
    <div class="font-box">
      <div class="font-auto">
        <van-slider
          @change="onChange"
          :max = maxZsc
          v-model="currSc"
          bar-height="4px"
          active-color="#ee0a24"
        >
          <template #button>
            <div class="custom-button"></div>
          </template>
        </van-slider>
        <audio
          ref="audioSrc"
          @play="onPlay"
          :src="ListenDwon[this.idBook].downloadUrl"
        ></audio>
        <div class="font-sc">
          <div class="dqsc">{{ dqsc }}</div>
          <div class="zsc">{{ zsc }}</div>
        </div>
      </div>
      <div class="font-ztp">
        <div class="font-ms">
          <img class="auto-img" src="../assets/tsml.png" alt="" />
        </div>
        <div class="font-up">
          <img src="../assets/up.png" alt="" />
        </div>
        <div class="font-pa" @click="getAddevent">
          <img src="../assets/play.png" alt="" />
        </div>
        <div class="font-next">
          <img src="../assets/next.png" alt="" />
        </div>
        <div class="font-ms">
          <img class="auto-img" src="../assets/nz.png" alt="" />
        </div>
      </div>
      <div class="font-spx">
        <div class="font-jsj">
          <div class="jsj-img">
            <img class="auto-img" src="../assets/sj_x.png" alt="" />
          </div>
          <div class="jsj-text">已加入书架</div>
        </div>
        <div class="font-jsj">
          <div class="jsj-img">
            <img class="auto-img" src="../assets/xz.png" alt="" />
          </div>
          <div class="jsj-text">批量下载</div>
        </div>
        <div class="font-jsj">
          <div class="jsj-img">
            <img class="auto-img" src="../assets/pl.png" alt="" />
          </div>
          <div class="jsj-text">评论</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ListenBook",

  data() {
    return {
      id: "",
      ListenData: [],
      catalogsData: [],
      cataLogId: [],
      ListenDwon: [],
      idBook: 0,
      bgc: "",
      Dura: 0,
      maxZsc:0,
      currSc:0,
      zsc: "00:00",
      dqsc: "00:00",
      isBf: false,
    };
  },

  created() {
    this.id = this.$route.params.id;
    this.getListenData();
    this.getArticle();
    this.getDownyp();
  },

  methods: {
    getAddevent() {
      let aud = this.$refs.audioSrc;
      this.formatSeconds(aud.duration);
      this.maxZsc = aud.duration;
      this.zsc = this.Dura;
      if (this.isBf) {
        aud.pause();
        this.isBf = false;
      } else {
        aud.play();
      }
    },

    onPlay(){
      this.getSc();
    },

    getSc(){
      setInterval(() =>{
        let aud = this.$refs.audioSrc;
        this.isBf = true;
        this.currSc = aud.currentTime;
        this.formatSeconds(aud.currentTime);
        this.dqsc = this.Dura
      },100)
    },

    onChange(value) {
      let aud = this.$refs.audioSrc;
       aud.currentTime = value;
      console.log(value);
    },

    formatSeconds(value) {
      var secondTime = parseInt(value); // 秒
      var minuteTime = 0; // 分
      var hourTime = 0; // 小时
      if (secondTime > 60) {
        //如果秒数大于60，将秒数转换成整数
        //获取分钟，除以60取整数，得到整数分钟
        minuteTime = parseInt(secondTime / 60);
        //获取秒数，秒数取佘，得到整数秒数
        secondTime = parseInt(secondTime % 60);
        //如果分钟大于60，将分钟转换成小时
        if (minuteTime > 60) {
          //获取小时，获取分钟除以60，得到整数小时
          hourTime = parseInt(minuteTime / 60);
          //获取小时后取佘的分，获取分钟除以60取佘的分
          minuteTime = parseInt(minuteTime % 60);
        }
      }
      var result = "" + parseInt(secondTime) + "秒";

      if (minuteTime > 0) {
        result = "" + parseInt(minuteTime) + "分" + result;
      }
      if (hourTime > 0) {
        result = "" + parseInt(hourTime) + "小时" + result;
      }
      return this.Dura = result;
    },

    onClickLeft() {
      this.$router.go(-1);
    },

  async getArticle() {
    await this.axios.get("/ting/catalog.json?sourceUuid=" +
          this.id).then(re => {
          console.log(re);
          this.catalogsData = re.catalogs;
          for (var i = 0; i < this.catalogsData.length; i++) {
            this.cataLogId.push(this.catalogsData[i].md5);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

   async getDownyp() {
     await this.axios.get("/ting/download.json?articleUuid:" +
          this.cataLogId[this.idBook]).then(re => {
          console.log(re);
          this.ListenDwon = re.urls;
          console.log(
            "this.ListenDwon[this.idBook]==>",
            this.ListenDwon[this.idBook]
          );
        })
        .catch((err) => {
          console.log(err);
        });
    },

   async getListenData() {
     await this.axios.get( "/ting/source.json?sourceUuid=" +
          this.id,).then(re => {
          this.ListenData = re.sources[0];
          console.log(this.ListenData);
          this.bgc = this.ListenData.cover;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.van-nav-bar__title {
  color: #fff;
}

.font-sc {
  display: flex;
  align-items: center;
  padding: 10px 0;
  .dqsc {
    flex: 1;
  }
  .zsc {
    margin-left: auto;
  }
}

.custom-button {
  width: 15px;
  height: 15px;
  color: #fff;
  background-color: #ee0a24;
  border-radius: 50%;
}

.ListenBook {
  padding: 10px;
  position: relative;
  color: #fff;
  background-color: unset;
  .bg {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    filter: blur(10px);
    z-index: -1;
    background-size: cover;
  }
  .listen-img {
    margin: 20px auto;
    width: 120px;
    height: 150px;
  }
  .listen-nt {
    margin: 0 auto;
    text-align: center;
  }
  .listen-box {
    margin-top: 50px;
    display: flex;
    align-items: center;
    .listen-hc {
      flex: 1;
      .listen-h {
        padding-bottom: 5px;
      }
    }
    .listen-ap {
      flex: 1;
      .listen-anchor {
        padding-bottom: 5px;
      }
    }
  }
  .listen-content {
    margin-top: 10px;
    line-height: 20px;
    .content-box {
      color: #666;
      overflow: hidden;
      text-overflow: ellipsis; // text-overflow css3的属性，当文本溢出时，显示省略号
      display: -webkit-box;
      -webkit-line-clamp: 4; // 设置两行文字溢出
      -webkit-box-orient: vertical;
      font-size: 12px;
    }
  }
  .font-box {
    position: fixed;
    bottom: 0;
    left: 0;
    width: calc(100% - 20px);
    padding: 10px;
    .font-ztp {
      margin-top: 15px;
      display: flex;
      align-items: center;
      justify-content: space-around;
      padding-bottom: 15px;
      .font-ms {
        width: 20px;
        height: 20px;
      }
    }
    .font-spx {
      display: flex;
      align-items: center;
      justify-content: space-around;
      .font-jsj {
        text-align: center;
        .jsj-img {
          width: 20px;
          height: 20px;
          justify-items: center;
          margin: 0 auto;
          padding-bottom: 10px;
        }
      }
    }
    .audio-box {
      margin-bottom: 20px;
    }
  }
}

.van-nav-bar{
  background-color: rgba(0, 0, 0, 0);
}

</style>
